body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #0c0c0c;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.form-control {
  border: #6590ba solid 1px;
  border-radius: 0;
  border-top: none;
  border-right: none;
  border-left: none;
}
.search-field {
  background-color: #030f28db;
  border-radius: 2px;
  margin-top: 80px;
  color: white;
  border: 2px solid #233451;
}
.search-title {
  color: white;
}
.search-field .app-select {
  color: #0c0c0c;
  font-weight: bolder;
}
.section-gap {
  padding: 30px 0px;
}
.default-header {
  /* box-shadow: none; */
  border-bottom: solid #c92828;
}
.nav-menu a {
  color: #0c0c0c;
}
.sticky-wrapper {
  height: 0px !important;
}
.top-section-gap {
  padding: 110px 0px;
}
.banner-area {
  margin-top: 0;
}
.irs-grid-text,
.irs-grid-pol {
  color: white;
  font-weight: bolder;
}

.primary-btn {
  border-radius: 0px;
  background: #c92927;
}
.onoffswitch3-active .onoffswitch3-switch {
  background: #c92927;
}
.onoffswitch3-inactive .onoffswitch3-switch {
  background: #c92927;
}
.images span {
  background: #c92927;
}
p {
  font-weight: bolder;
}
.align-items-end {
  -webkit-align-items: center !important;
          align-items: center !important;
}

.input-range__track {
  border-radius: 0rem !important;
  height: 1rem !important;
}
.input-range__track--active {
  background: #c92927 !important;
}
.input-range__slider {
  background: #5d2816 !important;
  border: none !important;
  margin-top: -1rem !important;
  border-radius: 0 !important;
}
.input-range__label {
  color: white !important;
  /* font-weight: bold !important; */
}
.input-range__label--min,
.input-range__label--max {
  bottom: -1.6rem !important;
}
.input-range__label--max {
  right: 1.6rem !important;
}
.input-range__label--min {
  left: 1rem !important;
}
.input-range__label--value {
  top: -2.1rem !important;
}
.input-serach-ptype {
  margin-top: 55px;
}
.toggle-wrap {
  margin-bottom: 0px;
}
@media (max-width: 991px) {
  .input-serach-ptype {
    margin-top: 0;
  }
}

